// Breadcrumb Component
// -------------------------

// REQIURES...
// $grey
// $light-grey
//
// $spacing-3
//
// @mixin font-standard-3
// @mixin chevron-right-1


.breadcrumbs {
  background-color: $white;
  border-bottom: $light-grey solid 2px;
  padding: $spacing-3 0;

  a {
    color: $grey;
  }

  .separator {
    @include right-chevron-2($light-grey);
    box-sizing: initial;
    overflow: hidden;
    text-indent: -1000px;
  }

  span:last-child:after {
    border: none;
  }
}


// MOBILE
@media (max-width: 480px) {
  .breadcrumbs {
    padding: $spacing-4 0;
    
    a,
    span {
      @include body-s-mob;
    }
  }
}
